@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:ital,wght@1,900&family=Sora:wght@600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Sora', sans-serif;
  /* background: #1d1f30; */
  background-color: #fdfdfd;
  color: #303549;
}

header {
  padding: 50px 15px 0px;
}

header h1 {
  color: #5796aa;
  font-size: 48px;
  font-weight: bolder;
  text-align: center;
  font-family: 'Alegreya Sans SC', sans-serif;
}

main {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

section {
  padding: 15px;
}

.searchbox {
  display: block;
  width: 100%;
  padding: 15px;

  border: none;
  outline: none;
  background: none;

  background-color: #eee;
  border-radius: 8px;

  color: #53565a;
  font-size: 20px;
  font-weight: 300;

  transition: 0.4s ease-out;
}

.searchbox:focus {
  box-shadow: 0px 0px 2px 1px #223343;
}

.results {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
  justify-content: center;
}

.results .result {
  width: 25%;
  min-width: 314px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.results .result img {
  display: block;
  width: 100%;
  height: 200px;
  border-radius: 24px;

}
/* enlarge image on hover
.results .result img:hover {

  border-radius: 0px;
  height: fit-content;

} */

.results .result h3 {
  top: 20px;
  left: 20px;
  text-transform: uppercase;
  color: #303549;
  font-size: 0.9em;
    font-weight: bolder;
    margin: 0.5em;
}

.results .result:hover h3 {
  font-weight: 700;
  color: #5796aa;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  background-color: #fdfdfd;
  color: #303549;

  overflow-y: scroll;
}

.popup .content {
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 25px;

}

.popup .content h2 {
  font-size: 28px;
  font-weight: 600;
}
.popup .content h2 span {
  font-weight: 300;
  font-size: 20px;
}

.popup .content .rating {
  margin-bottom: 30px;
}

.popup .content .plot {
  flex-wrap: wrap;
  margin: 0px -15px 30px;
}

.popup .content .plot img {
  flex: 1 1 50%;
  max-width: 300px;
  padding: 10px 15px 0 15px;
}

.popup .content .plot p {
  flex: 1 1 50%;
  padding: 15px;
}
h5 {
  padding: 15px 15px 0 15px;
}

.close {
  display: inline-block;
  padding: 7px 18px;
  font-size: 18px;
  font-weight: 700;
  background: #5796aa;
  color: #fff;
  border-radius: 8px;
  border: none;
  outline: none;
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  transition: 0.4s ease-out;
}

.popup .content .close:hover {
  background: #223343;
}
.fa-star {
  color: #ffd700;
  padding: 0 10px;
  font-size: 12px;
}
span {
  color: #aeb1b6;
  font-size: 0.8em;
}
.movie-gen li,
.label {
 
  font-size: 12px;
  color: #acacac;
  width: auto;
  display: block;
  float: left;
  margin-right: 6px;
  font-weight: 600;
  display: block;
}
.col_1 {
  margin: 0% 0.5% 0.5% 0.5%;
  display: block;
}

.year {
  float: right;
  padding: 0 12px 0 0;
}
@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .results .result {
    margin: auto;
  }
}
footer {
  display: flex;

  height: 80px;
  text-align: center;
 
  align-items: center;
  justify-content: space-between;
  margin: auto;
  position: relative;
  bottom: 0;
  width: 100%;
 
}

footer a,
footer p {
  color: #1D2024;
  font-size: 0.9em;
  text-decoration: none;
}
footer a:hover {
  text-decoration: none;
  color: #5796aa;
}
footer p {
  margin-left: 30px;
  margin-bottom: 0;
}
.git {
  margin-right: 30px;
}

